import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Dropdown } from 'react-bootstrap';
import classNames from 'classnames';
import FeatherIcon from 'feather-icons-react';

//TYPES 
import { LanguagesType } from "@components/LanguageDropdown/interfaces/index.interfaces";
//FLAGS

import trFlag from "@components/LanguageDropdown/flags/turkey.png";
import enFlag from "@components/LanguageDropdown/flags/us.jpg";

// get the languages
const Languages: LanguagesType = [
    {
        name: 'Turkish',
        flag: trFlag,
    },
    {
        name: 'English',
        flag: enFlag,
    }
];

const LanguageDropdown = () => {
    const [dropdownOpen, setDropdownOpen] = useState<boolean>(false);

    /*
     * toggle language-dropdown
     */
    const toggleDropdown = () => {
        setDropdownOpen(!dropdownOpen);
    };

    return (
        <Dropdown show={dropdownOpen} onToggle={toggleDropdown}>
            <Dropdown.Toggle
                id="dropdown-languages"
                as="a"
                onClick={toggleDropdown}
                className={classNames('nav-link', 'cursor-pointer', { show: dropdownOpen })}>
                <FeatherIcon icon="globe" />
            </Dropdown.Toggle>
            <Dropdown.Menu className="dropdown-menu dropdown-menu-end">
                <div onClick={toggleDropdown}>
                    {(Languages || []).map((lang, i) => {
                        return (
                            <Link to="/" className="dropdown-item" key={i + '-lang'}>
                                <img src={lang.flag} alt={lang.name} className="me-1" height="12" />{' '}
                                <span className="align-middle">{lang.name}</span>
                            </Link>
                        );
                    })}
                </div>
            </Dropdown.Menu>
        </Dropdown>
    );
};

export default LanguageDropdown;
